nav{
    min-height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-around;

    button{
        background: transparent;
        border: 2px solid rgb(65, 65, 65);
        cursor: pointer;
        padding:0.5rem;
        transition: all 0.5s ease;
        &:hover{
            background-color: rgb(65, 65, 65);
            color: white;
        }
    }
}

@media screen and (max-width:768px){
    nav{
        button{
            z-index: 100;
        }
    }
}